import React, { useState, useEffect } from "react";
import Layout from "./../components/Layout/Layout";
import axios from "axios";
import { useParams, useNavigate } from "react-router-dom";

const BookDetails = () => {
    const params = useParams();
    const navigate = useNavigate();
    const [book, setBook] = useState({});
    const [relatedProducts, setRelatedProducts] = useState([]);

    //initalp details
    useEffect(() => {
        if (params?.slug) getProduct();
    }, [params?.slug]);

    //getProduct
    const getProduct = async () => {
        try {
            const { data } = await axios.get(
                `${process.env.REACT_APP_API}/api/v1/book/get-book/${params.slug}`
            );
            setBook(data?.book);
            getSimilarProduct(data?.book._id, data?.book.semester._id);
        } catch (error) {
            console.log(error);
        }
    };

    //get similar product
    const getSimilarProduct = async (pid, cid) => {
        try {
            const { data } = await axios.get(
                `${process.env.REACT_APP_API}/api/v1/book/related-book/${pid}/${cid}`
            );
            setRelatedProducts(data?.books);
        } catch (error) {
            console.log(error);
        }
    };
    return (
        <Layout>
            <div className="row container mt-2">
                <div className="col-md-6">
                    <img
                        src={`${process.env.REACT_APP_API}/api/v1/book/book-photo/${book._id}`}
                        className="card-img-top"
                        alt={book.name}
                        height="400"
                        width={"350px"}
                    />
                </div>
                <div className="col-md-6 ">
                    <h1 className="text-center">Book Details</h1>
                    <h6>Name : {book.name}</h6>
                    <h6>Description : {book.description}</h6>
                    <h6>Price : {book.price}</h6>
                    <h6>Semester : {book?.semester?.name}</h6>
                    <h6>Owner : {book.owner}</h6>
                    <button class="btn btn-secondary ms-1">ADD TO CART</button>
                </div>
            </div>
            <hr />
            <div className="row container">
                <h6>Similar Semester Books</h6>
                {relatedProducts.length < 1 && (
                    <p className="text-center"><h1>No Similar Semester Books found</h1></p>
                )}
                <div className="d-flex flex-wrap">
                    {relatedProducts?.map((p) => (
                        <div className="card m-2" style={{ width: "18rem" }}>
                            <img
                                src={`${process.env.REACT_APP_API}/api/v1/book/book-photo/${p?._id}`}
                                className="card-img-top"
                                alt={p.name}
                            />
                            <div className="card-body">
                                <h5 className="card-title">{p.name}</h5>
                                <p className="card-text">{p.description.substring(0, 30)}...</p>
                                <p className="card-text"> ₹ {p.price}</p>
                                {/* <p className="card-text"> ₹ {p.owner}</p> */}
                                <button
                                    className="btn btn-primary ms-1"
                                    onClick={() => navigate(`/book/${p.slug}`)}
                                >
                                    More Details
                                </button>
                                <button class="btn btn-secondary ms-1">ADD TO CART</button>
                            </div>
                        </div>
                    ))}
                </div>
            </div>
        </Layout>
    );
};

export default BookDetails;